'use client'
import Rating from '@mui/material/Rating';
import React, { useContext } from 'react'
import { Navigation, Pagination, Scrollbar, A11y } from "swiper/modules";
import { Swiper, SwiperSlide } from "swiper/react";
import { CartContext } from '@/context/CartConetxt';
import toast from 'react-hot-toast';
import { useSession } from 'next-auth/react';
import Image from 'next/image';
import { addToCart } from '@/lib/services/cart.services';

import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/pagination";
import "swiper/css/scrollbar";

export default function ProductDetails_show({ data }) {
  const { setNoCartItems, setCartItems, setPrice } = useContext(CartContext);
  const { data: session } = useSession();
  const isLoggedIn = !!session?.user;

  const addProduct = async (id: string) => {
    if (!session?.token) {
      toast.error("You should login first");
      return;
    }

    const res = await addToCart(id, session.token);
    if (res?.status === 'success') {
      setNoCartItems(res.numOfCartItems);
      setCartItems(res);
      setPrice(res?.data?.totalCartPrice);
      toast.success('Successfully added to cart!');
    } else {
      toast.error("Failed to add to cart");
    }
  };

  if (!data) return null;

  return (
    <div className='flex flex-col justify-between w-11/12 m-auto mt-4 md:flex-row'>
      <div className='w-full md:w-7/12'>
        <Swiper modules={[Navigation, Pagination, Scrollbar, A11y]} spaceBetween={0} slidesPerView={1} navigation>
          {data.images.map((ele: string) => (
            <SwiperSlide key={ele}>
              <div className="relative w-full h-[500px]">
                <Image src={ele} alt={ele} fill />
              </div>
            </SwiperSlide>
          ))}
        </Swiper>
      </div>

      <div className='w-full md:w-4/12 flex flex-col gap-3 p-5'>
        <h1 className='text-3xl font-bold'>{data.title}</h1>
        <div className='flex'>
          <Rating name="half-rating-read" defaultValue={data.ratingsAverage} precision={0.5} readOnly />
          <p className='text-gray-400'>({data.ratingsQuantity}<span> reviews</span>)</p>
        </div>
        <p className='text-l text-green-300'>
          <span className='text-green-300'>In stock:</span> {data.quantity}
        </p>
        <p className='text-xl'>
          <span className='font-bold'>Price: </span>{data.price} EGY
        </p>
        <p className='text-xl'>
          <span className='font-bold'>Brand:</span> {data.brand.name}
        </p>
        <p className='text-l'>{data.description}</p>
        <hr />

        {isLoggedIn && (
          <button
            onClick={() => addProduct(data._id)}
            className='px-3 py-1 border-none text-white bg-red-400 hover:cursor-pointer hover:bg-red-500'
          >
            Buy now
          </button>
        )}

        <div className='relative'>
          <Image src='/Frame 911.png' alt='delivery' width={400} height={100} />
        </div>
      </div>
    </div>
  );
}
